@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: url("../../fonts/Montserrat-Light.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Merriweather";
  font-style: italic;
  font-weight: 300;
  src: url("../../fonts/Merriweather-LightItalic.woff") format("woff");
}
@font-face {
  font-family: "HelveticaNeueProLt";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/HelveticaNeueLTProLt.woff") format("woff");
}
@font-face {
  font-family: "HelveticaNeueProMd";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/HelveticaNeueLTProMd.woff") format("woff");
}
@font-face {
  font-family: "HelveticaNeuePro";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/HelveticaNeueLTProRoman.woff") format("woff");
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html.no-smooth {
  scroll-behavior: unset;
}

body {
  overflow-x: hidden;
  min-width: 100%;
}

img {
  border: 0;
  vertical-align: top;
}

a {
  text-decoration: none;
  color: inherit;
}

a[href^=tel] {
  text-decoration: inherit;
  color: inherit;
}

ul,
ol,
li {
  list-style: none;
}

button, a {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  border: none;
  cursor: pointer;
}

.header {
  color: #989696;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 17px;
  text-transform: capitalize;
  height: 54px;
}
.header .header-container {
  max-width: 641px;
  padding: 0 10px;
  margin: 0 auto;
}
.header .logo_header-pos {
  margin: 0 59px;
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu__item:nth-child(2), .menu__item:nth-child(6) {
  margin: 0 43px;
}
.menu__item:hover {
  color: #010101;
}

.burger-menu {
  display: none;
  width: 28px;
  height: 14px;
  cursor: pointer;
  position: absolute;
  right: 40px;
  z-index: 100;
  top: 50%;
  transform: translateY(-50%);
}

.burger-menu span {
  background: #000;
  height: 4px;
  position: absolute;
  width: 100%;
  left: 0;
  transition: all 0.3s ease;
}

.burger-menu span:first-child {
  top: 0;
}

.burger-menu span:nth-child(2) {
  top: 8px;
}

.burger-menu span:last-child {
  top: 16px;
}

.burger-menu-close span:first-child {
  transform: rotate(45deg);
  top: 8px;
}

.burger-menu-close span:nth-child(2) {
  opacity: 0;
}

.burger-menu-close span:last-child {
  transform: rotate(-45deg);
  top: 8px;
}

.menu-mobile {
  display: none;
}

.menu-mobile.menu-show {
  display: block;
  top: 0px;
  gap: 20px;
}
.menu-mobile.menu-show .menu-mobile__item {
  display: block;
}

body.no-scroll {
  overflow: hidden;
}

@media all and (max-width: 576px) {
  .burger-menu {
    display: block;
  }
  nav {
    height: 80px;
  }
  .menu-mobile {
    position: fixed;
    top: -100vh;
    left: 0;
    padding-top: 100px;
    z-index: 100;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    background: #eaebeb;
    transition: top 0.7s;
  }
  .header {
    position: relative;
    color: #1d1e1f;
  }
  .header .logo_header-pos {
    margin: 0 0px;
  }
  .header .menu {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header .menu__item:nth-child(2), .header .menu__item:nth-child(6) {
    margin: 0 0px;
  }
  .menu__item:not(.logo) {
    display: none;
  }
}
